.html, body {
  height: 100%;
  width: 100%;
  background: #75f5ea;
  }

.root {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container__img {
  width: 60%;
  height: 60%;
  position: relative;
  padding: 100px;
  text-align: center;
}

.imagem {
  padding: 5px;
  border: 2px dotted #933ec5;
  border-radius: 50%;
  width: 20%;
  box-shadow: 4px #000000, 2px 1px 6px 4px #000000(10, 10, 0, 0.5);
}

.content{
  margin-top: -10cm;
  padding: 300px;
  justify-content: center;
  flex-direction: column;
  overflow:hidden;
}


.content__nome{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #933ec5;
  text-align: center;
}

.content__txt{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-align: center;
}

.content__social {
  display:flex;
  list-style: none;
  justify-content: center;
  align-items: center;
  padding: 100px;
}

.content__social__item {
  margin-right: 10px;
}
